* {
	box-sizing: border-box;
	margin: 0;
}

body {
	font-family: "Inter", sans-serif;
	background-color: #e5e5e5;
	/* color: #2b283a; */

	padding: 0 20px;
}

.container {
	background-color: #ffff;
	max-width: 550px;
	margin: 50px auto;
}

/* Utilities */
/*************/
.bold {
	font-weight: 700;
}

/* Navbar */
/**********/
nav {
	background-color: #f55a5a;
	color: #ffff;
	padding: 20px;
	text-align: center;
}

nav p {
	font-weight: 300;
	letter-spacing: -0.075em;
}

nav .fa-globe-americas {
	margin-right: 7px;
}

/* Cards Section */
/*****************/
.cards-list {
	padding: 25px 40px 0px;
}

.card {
	font-size: 10px;
	display: flex;
	align-items: center;
	gap: 20px;
	padding: 20px 0 20px;
	border-bottom: 1px solid #f5f5f5;
}

.card:last-child {
	padding-bottom: 30px;
}

.card--image {
	height: 170px;
	width: 125px;
	/* preserve the original aspect ratio */
	object-fit: cover;
	border-radius: 5px;
}

.card--destination {
	display: flex;
	flex-wrap: nowrap;
	align-items: center;
}

.card--location .fas {
	color: #f55a5a;
	margin-right: 4px;
}

.card--location {
	letter-spacing: 0.17em;
}

.card--url {
	margin-left: 12px;
	color: #918e9b;
}

.card--title {
	font-size: 25px;
	line-height: 30px;
	margin: 5px 0 14px;
}

.card--date {
	margin-bottom: 9px;
}

.card--description {
	line-height: 1.5;
}
